export default {
	data () {
		return {
		}
	},
	mounted () {
		const id = this.$route.params.id
		this.$store.dispatch('getActivityShowImg',{ id })				
	},
	computed:{
		img () {
			return this.$store.state.activity.activityShowImg
		},
		status () {
			return this.$store.state.activity.activityShowStatus
		}
	}
}



